    if (darkMode.color == 'black') {
      setDarkMode({
        color: 'white',
        backgroundColor: 'black'
      })
      setBtnText("Light Mode")
    }
    else {
      setDarkMode({
        color: 'black',
        backgroundColor: 'white'
      })
      setBtnText("Dark Mode")
    }
